body {
  margin-top: 50px;
  margin-bottom: 80px;
  position: relative;
}

// DONOTUSE! Causes Bing Maps to FREAK OUT! Also, performance problems
//* {
//  @include transition(all 0.2s)
//}

dd {
  margin-bottom: 10px;
}

a.edit {
  white-space: nowrap;
}

h1 {
  padding-bottom: 10px;
  margin-bottom: 20px;
  border-bottom: 1px solid #ddd;
}

td {
  vertical-align: middle !important;
}

address {
  span {
    display: block;
  }
  i {
    display: block;
    font-size: 0.8em;
    color: $gray-light;
  }
}

fieldset {
  border: 1px solid #ddd;
  border-radius: 6px;
  padding: 12px;
}
legend {
  width: auto;
  padding: 6px 18px;
  margin-bottom: 16px;
  font-weight: bold;
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
  border-bottom: 0;
  text-align: center;

  span {
    margin-right: 10px;
  }

  .form-control {
    display: inline-block;
    width: auto;
    font-size: 21px;
    margin-left: 10px;
  }

  .btn {
    font-size: 20px;
    padding: 6px 12px;
  }
}