.nav-tabs {
    & > li > a {
        border: none;
        margin-right: 0;
        color: $navbar-default-link-color;
        @include transition(all 0.2s);
        &:hover {
            background-color: transparent;
            @include box-shadow(inset 0 -2px 0 $brand-primary);
            color: $brand-primary;
        }
        &.securus-admin-tab {
            background-color: $brand-primary;
            color: white;
        }
    }
    & > li.active > a {
        border: none;
        @include box-shadow(inset 0 -2px 0 $brand-primary);
        color: $brand-primary;
        &:hover {
            border: none;
            color: $brand-primary;
        }
        &:focus {
            border: none;
        }
    }
    & > li.disabled > a {
        @include box-shadow(inset 0 -1px 0 #ddd);
    }
    &.nav-justified {
        & > li > a,
        & > li > a:hover,
        & > .active > a,
        & > .active > a:hover {
            border: none;
        }
    }
}

.nav > li > a {
  padding-top: 5px;
  padding-bottom: 5px;
}

// Pills

.bsnav-pills {
    li {
        float: left;
        // Links rendered as pills
        a {
            border-radius: $nav-pills-border-radius;
            // todo(Sherif): Do we need this anymore?
            //      .info {
            //        color: #555;
            //      }
       
        }
        + li {
            margin-left: 2px;
        }
        // Active state
        &.active > a {
            &,
            &:hover,
            &:focus {
                color: $white;
                background-color: $brand-primary;
                .caret {
                    border-top-color: $nav-pills-active-link-hover-color;
                    border-bottom-color: $nav-pills-active-link-hover-color;
                }
            }
        }
    }
}

// Stacked pills
.bsnav-stacked {
    li {
        float: none;
        margin-top: 2px;
        + li {
            margin-top: 2px;
            margin-left: 0;
            // no need for this gap between nav items
       
        }
    }
}

.bsnav {
    margin-bottom: 0;
    padding-left: 0;
    // Override default ul/ol
    list-style: none;
    @include clearfix;
    * {
        transition: 0;
    }
    > li {
        position: relative;
        display: block;
        > a {
            position: relative;
            display: block;
            padding: $nav-link-padding;
            &:hover,
            &:focus {
                text-decoration: none;
                background-color: $nav-link-hover-bg;
            }
        }
        // Disabled state sets text to gray and nukes hover/tab effects
        &.disabled > a {
            color: $nav-disabled-link-color;
            &:hover,
            &:focus {
                color: $nav-disabled-link-hover-color;
                text-decoration: none;
                background-color: transparent;
                cursor: not-allowed;
            }
        }
    }
}

//Left navigation for phone numbers and call mgmt
.bsnav-pills > li.active > a,
.bsnav-pills > li.active > ul > li.active a {
    background-color: $brand-primary;
    color: white;
}

.bsnav-pills > li.active > ul > li.active a,
.bsnav > li > a:hover {
    background-color: $brand-primary;
    color: white;
}