// ------------------------------
//    Dropdown
// ------------------------------
.input-group .dropdown-toggle {
  background-color: $white;
  color: $btn-default-color;
  width: 100%;
  height: 34px;

  .selected-value {
    float: left;
  }

  .caret {
    margin-top: .5em;
    float: right;
  }
}

// ------------------------------
//    Multi-Select Dropdown
// ------------------------------
.dropdown-checkbox .dropdown-checkbox-content {
  border-top: 0;
}


.required:after {
  padding-left: 10px;
  font-weight: normal;
  font-size: 11px;
  color: #c0392b;
  content: 'Required';
}

.input-group-save {
  background-color: $brand-success;
  color: $white;
  border: 0;

  &:hover {
    background-color: darken($brand-success, 15%);
  }
}

//------------------------------
// Styling for Dropdown Checkbox
//------------------------------
.dropdown-checkbox .dropdown-checkbox-header {
  background-color: transparent;
  border-bottom: 1px solid #eee;
  padding: 5px 18px;

  .checkbox-all {
    width: 100%;
    &:after {
      float: left;
      content: 'Select All';
      margin-left: 30px;
      //      margin-top: -7px;
      margin-top: -.1em;
      line-height: 100%;
    }
  }

  .search {
    display: none;
  }
}

