.remove-override,
.enable-override {
    display: none;
}

.remove-override.visible,
.enable-override.visible {
    display: block;
}

.search-box,
.close-icon,
.search-wrapper {
    position: relative;
    padding: 10px;
}

.search-wrapper {
    margin: auto;
}

.search-box {
    width: 100%;
    border: 1px solid #ccc;
    outline: 0;
}

.close-icon {
    border: 1px solid transparent;
    background-color: transparent;
    display: inline-block;
    vertical-align: middle;
    outline: 0;
    cursor: pointer;
}

.close-icon:after {
    content: "X";
    display: block;
    width: 15px;
    height: 15px;
    position: absolute;
    background-color: #FA9595;
    z-index: 1;
    right: 35px;
    top: 0;
    bottom: 0;
    margin: auto;
    padding: 2px;
    border-radius: 50%;
    text-align: center;
    color: white;
    font-weight: normal;
    font-size: 12px;
    cursor: pointer;
}

.search-box:not(:valid) ~ .close-icon {
    display: none;
}
