.lbs {
    #map-canvas {
        width: 1140px;
        height: 350px;
        margin: 0px;
        padding: 0px;
        border-radius: 4px;
    }
}
.locating-feedback {
    width: 1140px;
    height: 100%;
    background-color: rgba(255, 255, 255, .85);
    z-index: 500;
    border: 1px solid #ddd;
    position: absolute;
    border-radius: 4px;
}
.locating-message {
    position: relative;
    top: 170px;
    text-align: center;
    font-size: 18px;
    font-weight: 400;
    color: #555;
    .fa-spin {
        color: #555;
    }
}
.controls {
    margin-top: 10px;
    border: 1px solid transparent;
    border-radius: 4px;
    height: 32px;
    outline: none;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
}
#pac-input {
    background-color: #fff;
    padding: 0 11px 0 13px;
    width: 400px;
    font-family: Roboto;
    font-size: 15px;
    font-weight: 300;
    text-overflow: ellipsis;
}
#pac-input:focus {
    border-color: #4d90fe;
    margin-left: -1px;
    padding-left: 14px;
    /* Regular padding-left + 1. */
    
    width: 401px;
}
.pac-container {
    font-family: Roboto;
}
#type-selector {
    color: #fff;
    background-color: #4d90fe;
    padding: 5px 11px 0px 11px;
}
#type-selector label {
    font-family: Roboto;
    font-size: 13px;
    font-weight: 300;
}
::-webkit-input-placeholder {
    color: #555;
    font-family: 'Source Sans Pro';
    font-weight: 400;
}
.map-controls {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    pointer-events: none !important;
    z-index: 9;
    width: 400px;
    position: absolute;
    pointer-events: none;
    display: block;
    left: 6%;
    margin: 80px 0 0 0;
}
.zoom-btn {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-transform-style: preserve-3d;
    -webkit-backface-visibility: hidden;
    border-radius: 4px;
    pointer-events: all;
    float: right;
    display: block;
    width: 60px;
    height: 60px;
    background: #0089cf url(../images/zoomBtn.png);
    border: 6px solid #0089cf;
    cursor: pointer;
}
.zoom-out {
    float: left;
    background-position: 0 -48px;
}
