.player {
  z-index: 2000;
  position: fixed;
  bottom: 0;
  left: 16.8%;
  width: 58.2%;
  padding: 15px;
  background-color: rgba(34, 35, 38, 1.0);
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;

  .inmate-video, .visitor-video, .call-playback {
    text-align: center;
    position: relative;
    margin-bottom: 20px;

    .no-margin {
      margin: 0;
    }

    .name {
      background-color: rgba(0, 0, 0, .65);
      bottom: 0;
      position: absolute;
      width: 100%;
      padding: 5px 5px 5px 10px;

      .resize {
        color: white;
        float: right;
        position: absolute;
        right: 14px;
        bottom: 14px;

        &:hover {
          color: $brand-primary;
        }
      }
    }

    .dl-horizontal {
      color: white;
      margin: 0;
      font-size: 12px;

      dt {
        width: 22%;
      }

      dd {
        color: #f3fffb;
        word-wrap: break-word;
        text-align: left;
        margin-left: 0;
      }
    }
  }

  .start-time, .end-time {
    margin: 0 10px;
    color: #f3fffb;
    display: inline-block;
  }

  .player-scrubber, .volume-scrubber {
    position: relative;
    height: 6px;
    width: 100%;
    border-radius: 5px;
    background-color: #404042;
    display: inline-block;

    .player-scrubber-button {
      position: absolute;
      top: -5px;
      left: 7.5%;
      border-radius: 20px;
      width: 17px;
      height: 17px;
      border: 2px solid $gray-lighter;
      background: $gray-lighter;

      &:active, &:hover {
        background-color: darken($brand-primary, 15%);
      }
    }

    .play-status {
      position: inherit;
      height: inherit;
      border-radius: inherit;
      width: 8%;
      background-color: $brand-primary;
    }

    .volume-status {
      position: inherit;
      height: inherit;
      border-radius: inherit;
      width: 8.5%;
      background-color: $brand-primary;
    }
  }

  .volume-scrubber {
    width: 100px;
  }

  .volume-position {
    margin-top: 7px;
  }

  .volume {
    border: 0;
    background: none;
    font-size: 20px;
    color: #dfe1e5;
  }

  .controls-container {
    padding: 10px 0 0 0;
    text-align: center;

    * {
      text-align: center;
      vertical-align: middle;
    }

    .play {
      position: relative;
      width: 40px;
      height: 40px;
      border-radius: 60px;
      border: 2px solid white;
      background: none;
      color: #dfe1e5;
      font-size: 20px;
      display: inline-block;

      &:active, &:hover {
        background-color: darken($brand-primary, 15%);
      }

      i {
        position: absolute;
        left: calc(50% - 6px);
        top: calc(50% - 10px);
      }
    }

    .rewind,
    .forward,
    .skip {
      width: 40px;
      height: 40px;
      border-radius: 40px;
      border: none;
      background: none;
      color: #dfe1e5;
      font-size: 20px;
      margin: 5px 0;
      display: inline-block;

      &:active, &:hover {
        background-color: darken($brand-primary, 15%);
      }

      i {
        margin-top: -4px;
      }
    }
  }

  .close-player {
    position: absolute;
    right: -15px;
    top: -15px;
    border: 0;
    font-size: 22px;
    background-color: rgba(34, 35, 38, 1.0);
    color: #dfe1e5;
    border-radius: 20px;
    padding: 0 7px;
  }
}