.dl-horizontal {
  margin: 0 0 5px;
  padding: 0;

  dt {
    float: left;
    width: 90px;
    clear: left;
    text-align: left;
    overflow: visible;
    white-space: normal;
    font-weight: normal;
  }

  dd {
    margin-left: 100px;
    margin-bottom: 0;
  }
}

.dl-timeline {
  margin: 10px 0 0 0;
  padding: 0;

  dt {
    float: left;
    width: 30px;
    clear: left;
    text-align: left;
    overflow: visible;
    white-space: normal;
    font-weight: normal;
  }

  dd {
    margin-left: 30px;
    margin-bottom: 0;
  }
}

.step-style {
  border-radius: 50%;
  width: 24px;
  height: 24px;
  padding: 2px;
  background: #eee;
  color: #555;
  text-align: center;
  display: inline-block;
  margin-right: 3px;
}

ul.call-tag {
  margin-top: 10px;
  padding: 0;

  * {
    vertical-align: middle;
    display: inline-block;
  }

  li.private {

    &:before {
      content: "\f007";
    }
  }

  li.public {

    &:before {
      content: "\f0c0";
    }
  }

  li {
    color: $gray-light;
    padding: 5px 10px 5px 5px;
    background: $gray-lighter;
    border-radius: 15px;
    font-size: 13px;
    border: 1px solid #dadada;
    margin-bottom: 5px;
    white-space: nowrap;

    &:before {
      font-family: FontAwesome;
      margin: 0 5px;
      -webkit-font-smoothing: antialiased;
      display: inline-block;
    }

    &:hover {
      background-color: $brand-primary;
      color: white;
      cursor: pointer;
      border: 1px solid darken($brand-primary, 5%);

      a {
        color: white;
        text-decoration: none;
      }

      a.delete {
        color: white;
      }
    }

    a {
      color: $gray-light;
      max-width: 150px;
      @include text-overflow();
    }

    a.delete {
      color: $gray-light;
      text-indent: 0;
      margin: -3px 0 0 5px;

      &:hover {
        color: darken($brand-danger, 15%);
      }
    }
  }
}

.item-list {
  margin-top: 20px;

  .item {
    margin-bottom: 20px;
  }
}

.item-list-message {
  margin-top: 20px;
}

.metadata {
  padding-bottom: 5px;
  margin-bottom: 5px;
  border-bottom: 1px solid #eee;

  .date-time {
    font-size: 12px;
  }

  .user {
    font-weight: bold;
  }

  .action {
    float: right;
  }

  hr {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}