.logo {
  text-indent: -999px;
  background: $securus-primary-blue url("../images/securus-logo-white.png") no-repeat center center;
  width: 74px;
  height: 30px;
  margin: 10px;
  display: inline-block;
}

.logo-securus {
  width: 80px;
  height: 80px;
  background-color: $securus-primary-blue;
  position: relative;

  $imageWidth: 70px;
  $imageHeight: 28px;

  &:after {
    content: " ";
    position: absolute;
    left: calc(50% - #{$imageWidth / 2});
    top: calc(50% - #{$imageHeight / 2});
    width: $imageWidth;
    height: $imageHeight;
    background: url(../images/logo-securus-white-transparent.png) no-repeat center center;
  }
}
