.page-load {
  position: absolute;
  top: 10px;
  left: calc(50% - 100px);
  z-index: 9999;

  .page-error {
    top: calc(50% - 20px);
  }
}

.notification-badge {
  margin-top: 3px;
  padding: 6px 10px;
  background-color: $securus-orange;
  font-size: 15px;
  border-radius: 15px;
  font-weight: normal;
}

.busy {
  position: absolute;
  left: calc(50% - 100px);
  top: calc(50% - 100px);
  width: 200px;
  height: 200px;
  background: rgba(0, 0, 0, .7);
  border-radius: 20px;

  > * {
    position: absolute;
    left: calc(50% - 20px);
    top: calc(50% - 25px);
    color: white;
  }
}

.busy {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, .9);
  z-index: 1000;
}
.busy .busy-icon {
  position: absolute;
  left: calc(50% - 7px);
  top: calc(50% - 7px);
  color: black;
}

.busy.page-busy {
  display: none;
}

.busy.section-busy {
  display: block;
}

.notify {
  position: absolute;
  left: 0;
  top: 65px;
  height: 32px;
  width: 100%;
  color: white;
  padding: 5px 10px;
  z-index: 999;
  text-align: center;
  line-height: 20px;
}
.notify .text,
.notify .close {
  color: white !important;
  display: inline-block;
}
.notify .close {
  opacity: .7 !important;
}

.notify.notify-info {
  background-color: #3498db;
  z-index: 960;
}
.notify.notify-alert {
  background-color: #c0392b;
  z-index: 970;
}
.notify.notify-system {
  background-color: #111;
  z-index: 980;
}
.notify.notify-success {
  background-color: #27ae60;
  z-index: 999;
}

.page-errors-warnings {
  position: fixed;
  width: calc(100% - 20px);
  top: 70px;
  left: 0;
  margin-left: 10px;
  margin-right: 10px;
  text-align: center;
}

.post-beta {
  display: none;
}
