/* ----------------------- */
/*    Custom Checkboxes    */
/*       Custom Radio      */
/* ----------------------- */
.rounded-check {
    background: none;
    display: block;
    position: relative;
    margin: 10px 0 0 0;
    padding-left: 40px;
    font-weight: normal;
    min-height: 18px;
    padding-top: 5px;
    line-height: 1.1;

  & > * {
        display: inline-block;
        vertical-align: top;
  }
  .icon {
      position: absolute;
      left: 1px;
      width: 30px;
      height: 30px;

      &:after {
           border-radius: 50%;
           border: 4px solid #d6d6d6;
           padding: 2px;
           color: transparent;
           background: white;
           font-family: FontAwesome;
           font-size: 15px;
           content: '\f00c';
           margin-right: 6px;
           -webkit-transition: color 1s;
           transition: color .3s;
       }
  }
  input[type=checkbox],
  input[type=radio] {
      position: absolute;
      opacity: 0;
      z-index: -1;
  }
  &:hover .icon:after {
       color: transparentize($brand-success, .9);
   }
  input[type=checkbox]:checked + .icon:after,
  input[type=radio]:checked + .icon:after {
      color: $brand-success;
  }
  input[type=checkbox][disabled] + .icon:after,
  input[type=radio][disabled] + .icon:after {
      background-color: #eee;
  }
  input[type=checkbox][disabled]:checked + .icon:after,
  input[type=radio][disabled]:checked + .icon:after {
      color: $gray-light;
      border-color: #ddd;
  }
  input[type=checkbox][disabled] + .icon:after,
  input[type=radio][disabled] + .icon:after  {
      color: transparent;
      border-color: #ddd;
  }
  .rows {
      display: inline-block;
      vertical-align: top;
      font-weight: normal;
      margin-bottom: 10px;

      .title {
          font-weight: 500;
      }

      .desc {
          display: block;
          font-size: 1.1em;
      }
  }
}

.rounded-check-indent {
  margin-left:40px;
  margin-top: 5px;
}

.table .rounded-check {
  margin: 0;
}

/* ---------------------------- */
/*    Custom Checkboxes         */
/*    Public/Private Toggle     */
/* ---------------------------- */
.sharing-check {
  background: #E3EFF5;
  display: block;
  position: relative;
  border-right: none;
  font-weight: normal;
  min-height: 18px;
  padding: 0;
  width: 90px;
  text-align: left;
  vertical-align: middle;
  display: inline-block;
  height: 34px;
  background-color: #eee;

  & > * {
    display: inline-block;
    vertical-align: top;
  }
  .icon {
    -webkit-font-smoothing: antialiased;
    margin: 7px 0 0 8px;
    font-size: 13px; 
    &:before {
      margin-right: 6px;
      color: #777;
      font-family: 'FontAwesome';
      font-size: 1em;
      content: '\f0c0';
    }
    &:after {
      color: #777;
      font-family: 'FontAwesome';
      font-size: 1em;
      content: 'Public \25BE';
    }
  }
  input[type=checkbox],
  input[type=radio] {
    position: absolute;
    opacity: 0;
    z-index: -1;
  }
  input[type=checkbox]:checked + .icon:before,
  input[type=radio]:checked + .icon:before {
    content:'\f007';
  }
  input[type=checkbox]:checked + .icon:after,
  input[type=radio]:checked + .icon:after {
    content:'Private \25BE';
  }
  input[type=checkbox][disabled] + .icon:before,
  input[type=radio][disabled] + .icon:before {
    color: #ccc;
  }
  input[type=checkbox][disabled] + .icon:after,
  input[type=radio][disabled] + .icon:after {
    color: #ccc;
  }
}
.btn-tag-add {
  height: 34px;
}


.view-only {
  label {
    margin-bottom: 2px;
  }
  .form-control {
    background-color: #eee !important;
    box-shadow: none;
    border-color: transparent;
    color: #333;
    margin-bottom: 20px;
    padding: 4px;
  }
}

.form-control-sm {
  width: 20%;
}

.form-control-md {
  width: 40%;
}

.form-control-lg {
  width: 80%;
}

.inline-center-vertically {
  line-height: 250%;
}


