.treeview {
  a {
    display: block;
    padding: 6px;
    border-bottom: 1px solid #eee;

    &:hover {
      text-decoration: none;
      background-color: $brand-primary;
      color: #fff;
    }

    &.active {
      background-color: $brand-primary;
      color: white;
      font-weight: bold;
      text-decoration: none;
      border-radius: 5px;
    }
  }

  ul ul a {
    padding-left: 40px;
  }

  ul {
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style-type: none;
  }

  li {
    padding: 0;
    margin: 0 0 -1px 0;
  }

  .add-treeview-row {
    display: none;
    padding: 10px;
    border-bottom: 1px solid #eee;

    &.show {
      display: block;
    }
  }

  .add-treeview-row input,
  .add-treeview-row .btn {
    padding-left: 6px;
    display: inline-block;
  }
  .add-treeview-row input {
    width: 70%;
  }
  .add-treeview-row .btn {
    width: 10%
  }

  .treeview-list {
    margin-left: 0;
    padding-left: 0;
    li {
      list-style-type: none;
    }
  }
}


.treeview-simple {
  ul {
    list-style-type: none;
    margin-left: 0;
    padding-left: 0;
    margin-bottom: 0;
  }

  ul ul li {
    padding-left: 15px;
    position: relative;
  }

  ul ul li:before {
    position: absolute;
    left: 2px;
    top: 0;
    content: " ";
    display: inline-block;
    width: 10px;
    height: 10px;
    border-bottom: 2px dotted #ccc;
    border-left: 2px dotted #ccc;
  }
}