.table > tbody > tr > td {
    padding: 6px;
}

tr {
    color: inherit;
}

.table > tbody > tr.active > td {
    background-color: #e8e8e8;
}

.table-hover > tbody > tr:hover > td,
.table-hover > tbody > tr:hover > th {
    background-color: #eee;
}

.table-below tr:first-child td {
    border-top: 0 !important;
}

.table-panel {
  border-collapse: collapse;
  margin-top: -10px;
}
.table-panel tbody:first-child  {
  border-top: 2px solid transparent !important;
}


//--------------------------------
//      Recent Activity
//--------------------------------

.table-recent-activity {
    tr {
        th:first-child, td:first-child, th:nth-child(n+5), td:nth-child(n+5) {
            text-align: center;
        }
    }
}

//--------------------------------
//      Security Roles
//--------------------------------

.table-security-permission {
    tr {
        th:last-child, td:last-child {
            width: 120px;
            text-align: center;
        }
    }
    label.rounded-check {
        margin: 0 0 7px 36px;
    }
}

//--------------------------------
//      Job Titles
//--------------------------------

.table-job-titles {
    tr {
        th:first-child, td:first-child {
            width: 35%;
        }
        th:nth-child(2), td:nth-child(2) {
            width: 35%;
        }
        th:last-child, td:last-child {
            width: 30%;
            text-align: center;
        }
    }
}

//--------------------------------
//      Access Location
//--------------------------------

.table-access-location {
    tr {
        th:first-child, td:first-child {
            width: 20%;
        }
        th:last-child, td:last-child {
            width: 30%;
            text-align: center;
        }
    }
}

//--------------------------------
//      Monitor Activity
//--------------------------------

.table-monitor-activity {
    tr {
        td {
            a.forward {
                position: relative;
                font-size: 12px;
                i {
                    position: relative;
                    left: 10px;
                }
                span.badge {
                    position: relative;
                    left: 5px;
                }
            }
            a.terminate-call {
                color: $brand-danger;
                font-size: 14px;
                &:hover {
                    color: darken($brand-danger, 15%);
                }
            }
        }
    }
}

//--------------------------------
//      Virtual Groups
//--------------------------------

.table-virtual-group-list {
    th:first-child, td:first-child {
        text-align: center;
        width: 35px;
    }
    th:nth-child(2), td:nth-child(2) {
        max-width: 100px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    th:nth-child(3), td:nth-child(3) {
        max-width: 250px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    th:nth-child(n+4), td:nth-child(n+4) {
        width: 150px;
        text-align: center;
    }
}

.table-virtual-groups {
    th:first-child, td:first-child {
        max-width: 150px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    th:nth-child(2), td:nth-child(2) {
        max-width: 150px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    th:nth-child(3), td:nth-child(3) {
        width: 120px;
        text-align: center;
    }
    th:last-child, td:last-child {
        width: 120px;
        text-align: center;
    }
}

//--------------------------------
//      Grievance
//--------------------------------

.table-sergeant-actions {
    tr:last-child td {
        border-bottom: 1px solid #ddd;
    }
    td:nth-child(1) {
        width: 60%;
        padding-left: 0;
    }
    td:nth-child(2) {
        width: 40%;
        padding-right: 0;
        text-align: right;
    }
}

.table-grievance-admin {
    tr:first-child td {
        border-top: 0 !important;
    }
    &.table-editable td {
        //    padding: 4px 0;
        height: 32px;
    }
    th:nth-child(1),
    td:nth-child(1) {
        width: 32px;
        text-align: center;
    }
}

.table-grievance-groups {
    tr td {
        height: 32px;
        border-top: 0 !important;
        border-bottom: 1px solid #ddd;
    }
    tr:last-child td {
        border-bottom: 0;
    }
}

//--------------------------------
//      SVV Visitors
//--------------------------------

.table-visitors {
    td:first-child, th:first-child {
        text-align: center;
        width: 40px;
    }
    td:last-child, th:last-child {
        text-align: center;
    }
    td:nth-child(2), th:nth-child(2) {
        text-align: center;
        width: 60px;
    }
}

.table-visitor-types {
    td:last-child, th:last-child {
        text-align: center;
    }
    td:first-child, th:first-child {
        text-align: center;
        width: 60px;
    }
}

.table-schedule-visitors {
    th:first-child {
        width: 40px;
        text-align: center;
        white-space: nowrap;
    }
    th:last-child, td:last-child {
        text-align: center;
        width: 160px;
    }
}

.table-schedule-inmate {
    th:last-child, td:last-child {
        text-align: center;
        width: 160px;
    }
}

.table-visit-schedule-confirmation {
    td:last-child {
        text-align: center;
    }
    td:first-child {
        color: $securus-gunmetal-gray;
        width: 225px;
    }
}

.table-visit-schedule-done {
    margin-bottom: 0;
    tr:first-child td {
        border-top: 0;
    }
    td:first-child {
        color: $securus-gunmetal-gray;
        width: 225px;
    }
}

//--------------------------------
//             LBS
//--------------------------------

.table-geofence {
    th:first-child {
        text-align: center;
        width: 60px;
    }
    td:first-child {
        text-align: center;
        font-size: 18px;
    }
    th:last-child, td:last-child {
        text-align: center;
    }
}

//--------------------------------
//      Inmate List
//--------------------------------

.table-inmate-list {
    th:first-child, td:first-child {
        text-align: center;
        width: 35px;
    }
    th:nth-child(2), td:nth-child(2) {
        text-align: center;
        width: 32px;
    }
    th:nth-child(9), td:nth-child(9) {
        text-align: center;
    }
}

//--------------------------------
//      CDR Search Results
//--------------------------------

.table-cdr-results {
    margin-top: 20px;
    th:first-child, td:first-child {
        text-align: center;
        width: 35px;
    }
    th:nth-child(2), td:nth-child(2), th:nth-child(n+7), td:nth-child(n+7) {
        text-align: center;
    }
    td:nth-child(4) {
        white-space: nowrap;
    }
    i {
        margin-left: 4px;
    }
}

//--------------------------------
//     Blocked Phone Numbers
//--------------------------------

.table-blocked-numbers {
    th:nth-child(n+2), td:nth-child(n+2), th:nth-child(-n+4), td:nth-child(-n+4) {
        white-space: nowrap;
    }
    th:nth-child(3), td:nth-child(3) {
        max-width: 150px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    th:nth-child(5), td:nth-child(5), div {
        max-width: 150px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        + div.tooltip {
            overflow: visible !important;
            text-overflow: nowrap;
            white-space: normal;
        }
    }
    th:nth-child(6), td:nth-child(6) {
        text-align: center;
    }
    th:nth-child(7), td:nth-child(7) {
        max-width: 150px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        i {
            padding-right: 8px;
        }
    }
    th:last-child, td:last-child {
        max-width: 100px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
}

//--------------------------------
//      Managed Phone Numbers
//--------------------------------

.table-managed-numbers {
    th:nth-child(n+2), td:nth-child(n+2), th:nth-child(-n+4), td:nth-child(-n+4) {
        white-space: nowrap;
    }
    th:nth-child(5), td:nth-child(5), th:nth-child(7), td:nth-child(7) {
        text-align: center;
    }
    th:nth-child(6), td:nth-child(6), div {
        max-width: 150px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        + div.tooltip {
            overflow: visible !important;
            text-overflow: nowrap;
            white-space: normal;
        }
    }
    th:nth-child(8), td:nth-child(8) {
        max-width: 150px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        i {
            padding-right: 8px;
        }
    }
    th:last-child, td:last-child {
        max-width: 100px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
}

//--------------------------------
//      Call Forward
//--------------------------------

.table-call-forward-results {
    th:nth-child(1) {
        width: 30%;
    }
    th:nth-child(2) {
        width: 20%;
    }
    th:nth-child(2) {
        width: 20%;
    }
    th:nth-child(3) {
        width: 30%;
    }
}

.table-end-call-forward td:nth-child(3) {
    text-align: center;
}

//--------------------------------
//      Users
//--------------------------------

.table-users {
    thead {
        tr {
            th:first-child, th:last-child, th:nth-child(6) {
                text-align: center;
            }
        }
    }
    tbody {
        tr {
            td:nth-child(3), td:nth-child(4) {
                max-width: 180px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            td:first-child, td:last-child, td:nth-child(6) {
                text-align: center;
            }
        }
    }
}

//--------------------------------
//      Service Tickets
//--------------------------------

.table-service-tickets {
    thead {
        tr {
            th:first-child {
                text-align: center;
            }
        }
    }
    tbody {
        tr {
            td:nth-child(3), td:nth-child(3) {
                max-width: 260px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            td:first-child {
                text-align: center;
            }
        }
    }
}

.table-service-tickets-closed {
    thead {
        tr {
            th:first-child {
                text-align: center;
            }
        }
    }
    tbody {
        tr {
            td:nth-child(3), td:nth-child(3) {
                max-width: 260px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            td:first-child {
                text-align: center;
            }
        }
    }
}

//--------------------------------
//      Covert Alerts
//--------------------------------

.table-covert-alerts-inmate {
    th:nth-child(1) {
        width: 30%;
    }
    th:nth-child(2) {
        width: 20%;
    }
    th:nth-child(3) {
        width: 10%;
        text-align: center;
    }
    td:nth-child(3) {
        text-align: center;
    }
}

.table-covert-alert-list {
    thead {
        tr {
            th:first-child, th:last-child {
                text-align: center;
            }
        }
    }
    tbody {
        tr {
            td:nth-child(3), td:nth-child(4) {
                max-width: 180px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            td:first-child, td:last-child {
                text-align: center;
            }
        }
    }
}

//--------------------------------
//      Inmate Profile
//--------------------------------

.table-call-schedule-holiday {
    th:first-child {
        width: 25%;
    }
    th:nth-child(2) {
        width: 45%;
    }
    th:last-child {
        width: 30%;
    }
}

.table-call-usage-detail tbody tr:last-child {
    font-weight: bold;
}

.table-pan thead th:last-child,
.table-pan tbody tr td:last-child {
    text-align: center;
}

.table-pan tbody tr td a i {
    position: relative;
    left: 10px;
}

.table-call-usage th,
.table-call-usage td {
    text-align: center;
}

.table-call-usage > thead > tr > th {
    line-height: 1em;
}

.table-covert-alerts-inmate td:nth-child(3),
.table-covert-alerts-inmate th:nth-child(3) {
    text-align: center;
}

//--------------------------------
//        Call Schedule
//--------------------------------

.schedule-scroll {
    height: 345px;
    overflow-y: auto;
    margin-bottom: 10px;
    border-bottom: solid 1px #ddd;
}

.table-date {
    margin-bottom: 0;
    tbody {
        tr {
            &:last-child {
                td {
                    border-bottom: none;
                }
            }
            td {
                padding: 1px 3px;
                border: solid 1px #ddd;
                &.time {
                    background-color: $brand-success;
                    border: none;
                }
            }
        }
    }
}

.child-row > td:first-child {
    padding-left: 40px;
}

//--------------------------------
//        PAN
//--------------------------------
.pan-table {
    margin-bottom: 0;
    table-layout: fixed;
    td {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}

//--------------------------------
//        Logging
//--------------------------------

.table-automated td:nth-child(2),
.table-automated th:nth-child(2) {
    text-align: center;
}

.table-slalom-log {
    th:nth-child(1) {
        width: 5%;
    }
    th:nth-child(2) {
        width: 10%;
    }
    th:nth-child(3) {
        width: 10%;
    }
    th:nth-child(4) {
        width: 15%;
    }
    th:nth-child(5) {
        width: 25%;
    }
    th:nth-child(6) {
        width: 15%;
    }
    th:nth-child(7) {
        width: 20%;
    }
}

//--------------------------------
//      Notifications
//--------------------------------

.table-notifications {
    tbody {
        tr {
            td {
                padding-top: 15px;
                padding-bottom: 15px;
            }
        }
    }
}

//--------------------------------
//        Commissary
//--------------------------------

.table-commissary {
    th:first-child, td:first-child {
        text-align: center;
        width: 35px;
    }
    th:last-child, td:last-child {
        text-align: center;
    }
}

.table-commissary-admin {
    th:first-child,
    td:first-child {
        text-align: center;
        width: 35px;
    }
    th:nth-child(2),
    td:nth-child(2) {
        width: 80px;
    }
    th:nth-child(4),
    td:nth-child(4) {
        text-align: center;
    }
    th:nth-child(5),
    td:nth-child(5) {
        text-align: center;
    }
    img {
        border: 1px solid #eee;
        padding: 5px;
        background-color: #fff;
    }
}

.commissary-shopping-cart {
    td:last-child {
        text-align: center;
    }
}

.table-editable {
    td {
        height: 51px;
        .row-actions {
            display: none;
            .btn {
                margin: 0;
                border: none;
            }
        }
    }
    tr {
        .row-actions {
            text-align: center;
        }
        &:hover .row-actions {
            display: block;
        }
    }
    .edit-row {
        text-align: center;
        background-color: rgba(52, 152, 219, 0.1) !important;
    }
}

.table-all-permissions {
    background-color: $gray-lighter;
    border-radius: 4px;
    a {
        font-weight: normal;
        color: $brand-primary;
    }
}

.hilite-row td {
    background-color: #feffcd;
}