.timeline {
    list-style: none;
    padding: 20px 0 20px;
    position: relative;
    margin-top: 20px;
    &:before {
        top: 0;
        bottom: 0;
        position: absolute;
        content: " ";
        width: 3px;
        background-color: $securus-platinum-gray;
        left: 5%;
        margin-left: -1.5px;
    }
    li {
        margin-bottom: 15px;
        position: relative;
        &:before, &:after {
            content: " ";
            display: table;
        }
        &:after {
            clear: both;
        }
        .timeline-badge {
            color: #fff;
            width: 50px;
            height: 50px;
            line-height: 50px;
            font-size: 1.4em;
            text-align: center;
            position: absolute;
            top: 0;
            left: 5%;
            margin-left: -25px;
            background-color: #999999;
            z-index: 100;
            border-top-right-radius: 50%;
            border-top-left-radius: 50%;
            border-bottom-right-radius: 50%;
            border-bottom-left-radius: 50%;
            &.dashboard-badge {
                width: 40px;
                height: 40px;
                line-height: 40px;
                left: 6.5%;
            }
            &.badge-date {
                color: $securus-gunmetal-gray;
                width: 30px;
                height: 30px;
                line-height: 29px;
                font-size: 16px;
                left: 6.75%;
                background-color: $securus-platinum-gray;
            }
            &.primary {
                background-color: $brand-primary !important;
            }
            &.success {
                background-color: $brand-success !important;
            }
            &.warning {
                background-color: $brand-warning !important;
            }
            &.danger {
                background-color: $brand-danger !important;
            }
            &.info {
                background-color: $brand-info !important;
            }
        }
    }
    li.timeline-inverted {
        &:first-child {
            margin-top: -25px;
        }
        .timeline-panel {
            background-color: white;
            width: 85%;
            float: right;
            border: 1px solid #d4d4d4;
            border-radius: 2px;
            padding: 15px;
            position: relative;
            -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
            box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
            &.list-only {
                padding: 0;
                background-color: transparent !important;
                box-shadow: none;
                border: none;
                margin-top: 0;
                &:after, &:before {
                    border: none;
                }
            }
            &:after,
            &:before {
                position: absolute;
                content: '';
                width: 0;
                height: 0;
                border-top-color: transparent;
                border-bottom-color: transparent;
                border-top-style: solid;
                border-bottom-style: solid;
                border-right-style: solid;
            }
            &:before {
                left: -10px;
                top: 16px;
                border-top-width: 10px;
                border-bottom-width: 10px;
                border-right-width: 10px;
                border-right-color: #d4d4d4;
            }
            &:after {
                left: -9px;
                top: 18px;
                border-top-width: 8px;
                border-bottom-width: 8px;
                border-right-width: 10px;
                border-right-color: white;
            }
            &.timeline-date {
                background-color: transparent !important;
                border: none;
                padding: 5px 15px 5px 0;
                -webkit-box-shadow: none;
                box-shadow: none;
                &:after, &:before {
                    position: absolute;
                    content: '';
                    width: 0;
                    height: 0;
                    border: 0;
                }
            }
            .timeline-heading {
                h4 {
                    margin-bottom: 0;
                    i {
                        margin-right: 5px;
                        &.fa-building8, &.fa-globe {
                            margin-right: 0;
                        }
                    }
                }
                .timeline-title {
                    margin-top: 0;
                }
            }
            .timeline-body {
                margin-top: 10px;
                p:last-child {
                    margin-bottom: 0;
                }
            }
        }
        .popover-markup {
            display: inline-block;
            a {
                img {
                    height: 50px;
                }
            }
        }
    }
}
