.popup {
    border: 1px solid #eee;
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
    padding: 0 15px;
    position: absolute;
    overflow: hidden;
    right: 0;
    top: 0;
    display: none;
    width: 100%;
    height: 100%;
    background-color: white;
    z-index: 999;
    border-radius: 4px;
}

.popover {
  max-width: 360px;
  margin-top: 30px;
  z-index: 8000;

  .popover-footer {
    margin: 0;
    padding: 8px 14px;
    background-color: $gray-lightest;
    border-top: 1px solid $gray-lighter;
    border-radius: 0 0 5px 5px;
  }
}

// NOTE:  This class must be applied to the <div> that is designated as the popover container;
// styling the .popover directly would change the styling for all popovers.
// See the 'container' property documentation: http://getbootstrap.com/javascript/#popovers
.popover.popover-primary {
  padding: 0;
  max-width: 1500px;

  .popover-title {
    background-color: $brand-primary;
    color: $white;

    .close {
      color: $white;
      font-size: 14px;
      opacity: 0.4;

      &:hover,
      &:focus {
        opacity: 0.8;
      }
    }
  }
}