.navbar {
  border: none;
  margin-bottom: 0;
  z-index: $navbar-z-index;
  background-color: $securus-platinum-gray;
  * {
    transition: none !important;
  }
  .navbar-header {
    background-color: $brand-primary;
  }
  .navbar-right {
    li a {
      border: none;
      img {
        margin-top: -4px;
      }
    }
  }
  li {
    a {
      border-left: none;
      padding: 15px 0;
//      font-family: 'Source Sans Pro';
      font-weight: 400;
      &:hover {
        transition: none !important;
        div.divider {
          border-left: 1px solid $brand-primary;
        }
        div.divider-last {
          border-right: 1px solid $brand-primary;
        }
        div.divider-first {
          border-left: none;
        }
      }
      div.divider {
        border-left: 1px solid #ccc;
        padding: 0 10px;
      }
      div.divider-last {
        border-right: 1px solid #ccc;
        padding: 0 10px;
      }
      div.divider-first {
        border-left: none;
      }
      span.fa  {
        margin-right: 3px;
      }
      &.right-nav-icon {
        color: lighten($gray, 30%);
        font-size: 18px;
        padding: 14px 7px;
        &:hover {
          background-color: transparent;
          color: $gray;
        }
        &.active {
          color: $securus-orange;
          &:hover {
            color: darken($securus-orange, 10%);
          }
        }
      }
    }
  }
  .dropdown {
    margin-bottom: 0;

    &.current-user-dropdown {
      .username {
        font-size: 14px;
      }
    }
  }
  .dropdown-menu {
    border: 0;
    a {
      text-align: left;
      padding: 5px 15px;
      &:hover, &:hover small, &:hover i {
        color: white;
      }
      &.right-nav-icon:hover {
        background-color: red;
      }
    }
    > li.notification-header {
      padding: 7px 14px;
      font-size: 16px;
      background-color: $securus-orange;
      color: white;
    }
    > li.notification-dropdown-item {
      & a {
        white-space: normal;
        width: 280px;
        color: $brand-primary;
        &:hover {
          color: white;
        }
        > .notification-icon {
          display: inline-block;
          width: 30px;
          vertical-align: middle;
        }
        > .notification-description {
          display: inline-block;
          width: 165px;
          vertical-align: middle;
          margin-left: 10px;
          p {
            margin: 0;
          }
        }
      }
    }
  }
}

.open > a {
  &,
  &:hover,
  &:focus {
    background-color: $nav-link-hover-bg;
    border-color: $link-color;

    .caret {
      border-top-color: $link-hover-color;
      border-bottom-color: $link-hover-color;
    }
  }
}


.open > a, .open > a:hover, .open > a:focus {
  background-color: transparent;
}


.subnav {
  list-style-type: none;
  margin-left: 0;
  padding-left: 0;
  border-top: 1px solid #ddd;

  li {
    padding: 5px 10px;
    border-bottom: 1px solid #ddd;
    color: #999;

    &.active {
      background-color: #3f5872;
      border-radius: 5px;

      a {
        color: white;
      }
    }
  }
}
