.month-container {
    width: 100%;
    .month {
        width: 100%;
        border: 1px solid #eee;
        th {
            text-align: center;
            padding: 1px;
        }
        td {
            text-align: center;
            border: 1px solid #eee;
            background-color: #fff;
            font-size: 16px;
            &.disabled {
                background-color: #ccc;
                padding: 8px;
            }
            &.disabled a {
                color: #111 !important;
            }
            &.disabled a:hover {
                background-color: #ccc;
                cursor: default;
            }
            a {
                display: block;
                width: 100%;
                padding: 8px;
                text-decoration: none !important;
                border: none !important;
                &:hover, &.selected {
                    background-color: $securus-primary-blue;
                    color: white;
                    font-weight: normal;
                    font-size: 16px;
                }
            }
        }
    }
}

.specify-schedule-container {
    height: 300px;
    overflow: auto;
}

.specify-schedule {
    cursor: pointer;
    border-bottom: 1px solid #ddd;
    .hour, .half-hour {
        height: 32px;
        width: 100px;
        border-right: 1px solid #eee;
    }
    td {
        background-color: #fff;
        vertical-align: top;
        padding-top: 0;
        padding-bottom: 10px !important;
        &:nth-child(1) {
            text-align: right;
        }
    }
    tr:hover td {
        background-color: $securus-green;
        color: white;
    }
    .unavailable td,
    .unavailable:hover td {
        background-color: #ddd;
        color: #aaa;
    }
    .tentative {
        border: 2px dashed #EDB4B4;
    }
    .scheduled td,
    .scheduled:hover td {
        background-color: $brand-success;
    }
    .scheduled td {
        color: #eee;
    }
    .date-container {
        max-height: 450px;
        overflow: auto;
    }
    .date {
        text-align: center;
    }
    .date-selected {
        background-color: green;
        color: white;
    }
}

.visit-schedule-finish.show {
    display: block;
}

.override-visit-restrictions {
    padding-top: 0 !important;
}

.multiselect-style {
    .multiselect {
        padding: 0;
        border: none;
    }
    &.visit-type-style {
        margin-right: 20px;
        .btn-group {
            border-bottom: none;
            width: auto;
            button {
                font-size: 14px;
                font-weight: 300;
            }
            .dropdown-toggle,
            .dropdown-toggle:active {
                max-width: none;
                width: 58px;
            }
        }
    }
    &.visit-status-style {
        .btn-group {
            border-bottom: none;
            width: auto;
            button {
                font-size: 14px;
                font-weight: 300;
            }
            .dropdown-toggle,
            .dropdown-toggle:active {
                max-width: none;
                width: 64px;
            }
        }
    }
    &.inmate-location-style {
        margin-right: 20px;
        .btn-group {
            border-bottom: none;
            width: auto;
            button {
                font-size: 14px;
                font-weight: 300;
            }
            .dropdown-toggle,
            .dropdown-toggle:active {
                max-width: none;
                width: 80px;
            }
        }
    }
    &.inmate-activity-style {
        .btn-group {
            border-bottom: none;
            width: auto;
            padding-right: 0;
            button {
                font-size: 14px;
                font-weight: 300;
                text-align: left;
            }
            .dropdown-toggle,
            .dropdown-toggle:active {
                max-width: none;
                width: 190px;
            }
        }
    }
    &.dashboard-activity-style {
        display: inline-block;
        .btn-group {
            border-bottom: none;
            width: auto;
            padding-right: 0;
            margin-top: 3px;
            button {
                font-size: 14px;
                font-weight: 300;
                text-align: left;
            }
            .dropdown-toggle,
            .dropdown-toggle:active {
                max-width: none;
                width: 160px;
                font-size: 14px;
            }
        }
    }
    &.restriction-style {
        .btn-group {
            width: auto;
            button {
                font-size: 20px;
                font-weight: 500;
            }
            .dropdown-toggle,
            .dropdown-toggle:active {
                max-width: none;
                width: 120px;
            }
        }
    }
    &.visit-restriction-style {
        .btn-group {
            width: auto;
            button {
                font-size: 20px;
                font-weight: 500;
            }
            .dropdown-toggle,
            .dropdown-toggle:active {
                max-width: none;
                width: 140px;
            }
        }
    }
    * {
        transition: 0;
    }
    label {
        font-size: 14px;
        font-weight: 300;
    }
    .btn-group {
        border-bottom: 1px dashed $brand-primary;
        padding: 0 10px;
        width: 110px;
        text-align: center;
        button {
            background: transparent;
            font-size: 14px;
            font-weight: 300;
            color: $brand-primary;
        }
        .dropdown-toggle,
        .dropdown-toggle:active {
            background: transparent;
            box-shadow: none;
            max-width: 90px;
            overflow: hidden;
            text-overflow: ellipsis;
            margin: 0 auto;
            width: 110px;
            margin-top: -2px;
        }
        li > a {
            padding: 0;
        }
    }
}

@media print {
    .navbar,
    .page-header,
    .btn-print,
    .btn-view-visits,
    .page-footer {
        display: none;
    }
    .securus-panel-style,
    .well {
        -webkit-box-shadow: none;
        box-shadow: 0 0 0 transparent;
        border: 0;
    }
}

.visit-schedule-list {
    padding: 10px;
    background-color: white;
    border-radius: 4px;
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
    display: flex;
    align-items: center;
    margin: 15px 0;
    border: 1px solid transparent;
    &:hover {
        cursor: pointer;
    }
    .time {
        font-size: 16px;
        font-weight: bold;
    }
    .location {
        font-size: 16px;
    }
    .details-box {
        padding: 0 15px 0 0;
        margin: 0;
        display: flex;
        align-items: center;
        img {
            border-radius: 4px;
            max-width: 100%;
            height: auto;
        }
        .details {
            margin-left: 10px;
            max-width: 100%;
            .visit-info {
                margin: 0;
                &:first-child dt, &:first-child dd {
                    font-weight: bold;
                }
                dt {
                    width: 60px;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                }
                dd {
                    margin-left: 60px;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                    &.dropdown {
                        overflow: visible !important;
                    }
                }
            }
        }
    }
    .status {
        font-size: 16px;
        float: right;
        color: $brand-success;
        text-align: right;
        span {
            display: block;
            font-weight: normal;
            color: $gray-light;
            font-size: 12px;
        }
        &.onhold {
            color: $brand-warning;
        }
        &.cancelled {
            color: $brand-danger;
        }
    }
}

.input2 {
    position: relative;
    z-index: 1;
    display: inline-block;
    margin: 1em;
    max-width: 350px;
    width: calc(100% - 2em);
    vertical-align: top;
}

.input__field {
    position: relative;
    display: block;
    float: right;
    padding: 0.8em;
    width: 60%;
    border: none;
    border-radius: 0;
    background: #f0f0f0;
    color: #aaa;
    font-weight: 400;
    font-family: "Avenir Next", "Helvetica Neue", Helvetica, Arial, sans-serif;
    -webkit-appearance: none;
    /* for box shadows to show on iOS */
}

.input__field:focus {
    outline: none;
}

.input__label {
    display: inline-block;
    float: right;
    padding: 0 1em;
    width: 40%;
    color: #696969;
    font-weight: bold;
    font-size: 70.25%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.input__label-content {
    position: relative;
    display: block;
    padding: 1.6em 0;
    width: 100%;
}
/* Individual styles */
/* Ruri */

.input__field--ruri {
    width: 100%;
    background: transparent;
    padding: 0.5em 0;
    margin-bottom: 2em;
    color: #fff;
}

.input__label--ruri {
    width: 100%;
    position: absolute;
    text-align: left;
    font-size: 1em;
    padding: 10px 0 5px;
    pointer-events: none;
}

.input__label--ruri::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 7px;
    background: #B7C3AC;
    left: 0;
    top: 100%;
    -webkit-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    -webkit-transition: -webkit-transform 0.3s, background-color 0.3s;
    transition: transform 0.3s, background-color 0.3s;
}

.input__label-content--ruri {
    padding: 0;
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    -webkit-transition: -webkit-transform 0.3s, color 0.3s;
    transition: transform 0.3s, color 0.3s;
}

.input__field--ruri:focus + .input__label--ruri::after,
.input--filled .input__label--ruri::after {
    background: #a3d39c;
    -webkit-transform: scale3d(1, 0.25, 1);
    transform: scale3d(1, 0.25, 1);
}

.input__field--ruri:focus + .input__label--ruri .input__label-content--ruri,
.input--filled .input__label--ruri .input__label-content--ruri {
    color: #a3d39c;
    -webkit-transform: translate3d(0, 2em, 0) scale3d(0.655, 0.655, 1);
    transform: translate3d(0, 2em, 0) scale3d(0.655, 0.655, 1);
}
