.steps {
  $step-width: 150px;
  $step-height: 70px;
  $step-color-default: $securus-platinum-gray;
  $step-color-completed: $securus-primary-blue;
  $step-color-active: $brand-success;
  $step-color-inverse: #fff;
  $step-z-index: 10;
  $step-triangle-width: 30px;

  * {
    transition: 0;
  }

  display: block;
  height: $step-height;
  margin-left: 10px;
  margin-bottom: 15px;
  position: relative;
  width: $step-width * 6;
  margin-left: auto;
  margin-right: auto;

  .step {
    position: absolute;
    top: 0;
    display: inline-block;
    margin-left: -10px;
    background-color: $step-color-default;
    width: $step-width;
    height: $step-height;
    text-align: left;

    &:first-child span {
      left: 20px;
    }

    .not-set {
      position: absolute;
      display: block;
      top: calc(50% - 10px);
      left: 45px;
      z-index: $step-z-index * 10;
    }

    .set {
      display: block;
      position: absolute;
      top: 20px;
      left: 45px;
      z-index: 200;

      .label {
        display: block;
        font-size: .8em;
        color: $text-color;
        text-align: left;
        padding: 0;
      }
      .desc {
        display: block;
        width: $step-width - 40px;
        font-size: 1.1em;
        overflow-x: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    .set.detailed {
      top: 10px;
      width: $step-width - 50px;
      .desc {
        .line1 {
          display: block;
        }
        .line2{
          display: block;
          font-size: .9em;
        }
      }
    }

    .triangle-container {
      position: absolute;
      width: $step-triangle-width;
      height: $step-height;
      right: -$step-triangle-width;
    }

    .triangle {
      width: 0;
      height: 0;
      border-style: solid;
      border-width: $step-height/2 0 $step-height/2 $step-triangle-width;
      border-color: transparent transparent transparent $step-color-default;
      position: absolute;
    }

    &.completed {
      background-color: $step-color-completed;
      color: $step-color-inverse;
      .label,
      .desc {
        color: $step-color-inverse !important;
      }

      .triangle {
        border-color: transparent transparent transparent $step-color-completed;
      }
    }

    &.active,
    &:hover {
      background-color: $step-color-active;
      color: $step-color-inverse !important;
      .label,
      .desc {
        color: $step-color-inverse !important;
      }

      .triangle {
        border-color: transparent transparent transparent $step-color-active;
      }
    }
  }

  .separator-triangle {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: ($step-height/2)+10px 0 ($step-height/2)+10px $step-triangle-width+5px;
    border-color: transparent transparent transparent $body-bg;
    position: absolute;
    top: -10px;
  }

  .step1 {
    left: 0;
    z-index: $step-z-index * 6;
  }

  .step2 {
    left: $step-width;
    z-index: $step-z-index * 5;
  }

  .step3 {
    left: $step-width * 2;
    z-index: $step-z-index * 4;
  }

  .step4 {
    left: $step-width * 3;
    z-index: $step-z-index * 3;
  }

  .step5 {
    left: $step-width * 4;
    z-index: $step-z-index * 2;
  }

  .step6 {
    left: $step-width * 5;
    z-index: $step-z-index;
  }
}

.table-schedule-inmate {
  th, td {
    white-space: nowrap;
  }

  th:nth-child(6),
  td:nth-child(6) {
    text-align: center;
  }

}