.securus-admin {
    margin-top: 20px;
    border-top: 2px solid #0074C8;
    position: relative;
    padding-top: 20px;
}
.securus-admin.securus-panel-padding {
    padding-top: 40px;
}
.securus-admin:before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    display: block;
    padding: 6px 12px;
    content: "Securus Admin Settings";
    color: white;
    background-color: #0074C8;
    font-size: 1.2em;
}
.securus-admin {
    display: none;
}
.securus-admin.visible {
    display: block;
}

.securus-admin-mode-link.badge {
  background-color: #0074C8;
  white-space: nowrap;
  display: inline-block;
  color: white;
}
.securus-admin-mode-off,
.securus-admin-mode-on {
  display: none;
}
.securus-admin-mode-off.visible,
.securus-admin-mode-on.visible {
  display: inline-block;
}