.col-btn-play {
  text-align: center;
}

.heading-btn {
  margin-top: -5px;
}

.btn-icon {
  background: transparent none;
  border: 0;
  text-decoration: none;
  position: relative;

  &:hover {
    @extend %transparent;
  }

  &:active {
    @extend %transparent;
  }

  &.btn-default {
    color: $brand-primary;
  }

  &.btn-danger {
    color: $sui-red;
  }

  .badge {
    position: absolute;
    left: 5px;
    top: initial;
    bottom: 2px;
  }
}

.btn-toggle {
  display: block;
  text-align: center;
  background: #eee;
  padding: 6px;
  margin-bottom: 24px;
}

.btn-add-to-col {
  display: block;

  & + & {
    margin-top: 20px;
  }
}

.btn-full-width {
  width: 100%;

  .icon {
    vertical-align: middle;
    margin-right: 5px;
  }
}

.btn-link-previous:before {
  content: '\2190';
}

.btn-link-next:after {
  content: '\2192';
}

.btn-link-danger {
  color: $brand-danger;

  &:hover {
    color: darken($brand-danger, 15%);
  }
}

.btn-add-entity {
  float: right;
  color: $brand-success;

  &:visited {
    color: $flat-emerald;
    text-decoration: none;
  }

  &:hover {
    color: $flat-emerald;
    text-decoration: none;
  }

  &:active {
    color: $brand-success;
    text-decoration: none;
  }
}

.btn-search {
  width: 100%;
}

.close-add-row {
  text-align: right;
}

.btn-success-outline {
  border: 1px solid $brand-success;
  color: $brand-success;

  * {
    transition: 0;
  }

  &:hover {
    color: darken($brand-success, 15%);
    border: 1px solid darken($brand-success, 15%);
  }

  &.confirmed {
    color: white;
    background-color: $brand-success;
    border-color: $brand-success;
  }
}

.btn-remove-entity {
  float: right;
  color: $flat-alizarin;

  &:visited {
    color: $flat-alizarin;
    text-decoration: none;
  }

  &:hover {
    color: $brand-danger;
    text-decoration: none;
  }

  &:active {
    color: $flat-alizarin;
    text-decoration: none;
  }
}

.link-cancel {
  margin-left: 20px;
}

.col-sort-by {
  border-left:1px solid #ccc;
}

.btn-row {
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 15px;

  .col-md-1,
  .col-md-2,
  .col-md-3,
  .col-md-4,
  .col-md-5,
  .col-md-6,
  .col-md-7,
  .col-md-8,
  .col-md-9,
  .col-md-10,
  .col-md-11,
  .col-md-12 {

    padding-left: 5px;
    padding-right: 5px;

    .btn {
      width: 100%;
    }
  }
}

.btn-permission {
  width: 100%;
  padding: 20px;
}

.btn-call-types {
  color: $brand-primary;
  background-color: transparent;
  border-bottom: 1px dashed $brand-primary;
  font-size: 20px;
  padding: 1px 3px;
}

.settings-headline {
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;

  .btn-link {
    font-size: 14px;
    margin-top: -5px;
  }
}


.btn-delete {
  color: $brand-danger;

  &:hover {
    color: darken($brand-danger, 15%)
  }
}

.btn-cdr {
  margin-top: 24px;
}

.btn-group-2 {
  .btn {
    width: 45%;
  }
  :first-child {
    margin-right: 5px;
  }
}

