.alert {
    padding: 0;
    .header {
        padding: 7px 14px;
        font-size: 16px;
        background-color: $brand-warning;
        color: white;
    }
    .item {
        display: block;
        margin: 0;
        padding: 0;
        border-bottom: 1px solid #e5e5e5;
        &:last-child {
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
            border-bottom: 0;
        }
        &:hover {
            background-color: $brand-primary;
        }
        > a, > .no-link {
            padding: 14px !important;
            margin: 0 !important;
        }
        > a:hover {
            background-color: $brand-primary;
            * {
                color: white !important;
            }
        }
        > .no-link:hover {
            background-color: #eee;
        }
        .icon {
            display: inline-block;
            padding: 0;
            margin: 0;
            vertical-align: middle;
            + .description {
                padding-left: 14px;
            }
        }
        .description {
            padding: 0;
            margin: 0;
            display: inline-block;
            vertical-align: middle;
        }
        a .description {
            color: $brand-primary;
        }
        .date-time {
            color: #777;
            font-size: .8em;
        }
    }
}

