.table-inmate-details {
  td:first-child {
    width: 30% !important;
  }
  td:nth-child(2) {
    width: 70%;
  }
}

#murray-video {
  width: 280px;
  height: 152px;
  vertical-align: middle;
}

.exists-on-other-pan {
  position: relative;
  left: -10px;
  top: 6px;
  background-color: $brand-warning;
}

.profile-edit {
  position: relative;

  img, video {
    border-radius: 4px;
    width: 100%;
    border: 5px solid darken($page-header, 10%);
    background-color: white;
    height: 152px;
  }

  a:hover {
    text-decoration: none;
  }

  .edit-photo-overlay {
    position: absolute;
    bottom: 5px;
    left: 5px;
    height: 45px;
    width: 253px;
    background-color: rgba(0, 0, 0, .1);
    z-index: 1020;

    .edit-photo-text {
      position: absolute;
      color: white;
      //      display: none;
      left: 44px;
      margin-top: -30px;
    }


    &:hover {
      transition: background-color 0.25s ease;
      background-color: rgba(0, 0, 0, .7);
    }

    .edit-photo-icon {
      padding: 15px;
      color: white;
    }

    &:hover > .edit-photo-icon {
      transition: all .3s cubic-bezier(.175, .885, .32, 1.275);
      @include scale(1.5);
    }

    &:hover > .edit-photo-text {
      //      display: block;

    }
  }
}

