// Padding
.no-padding {
  padding: 0;
}

.no-padding-top {
  padding-top: 0;
}

// Margins
.no-margin {
  margin: 0;
}

.no-margin-top {
  margin-top: 0;
}

.margin-top-5 {
  margin-top: 5px;
}

.margin-top-10 {
  margin-top: 10px;
}

.margin-top-20 {
  margin-top: 20px;
}

.no-margin-bottom {
  margin-bottom: 0;
}

.margin-bottom-5 {
  margin-bottom: 5px;
}

.margin-bottom-10 {
  margin-bottom: 10px;
}

.margin-bottom-20 {
  margin-bottom: 20px;
}

.margin-bottom-30 {
  margin-bottom: 30px;
}

.no-margin-left {
  margin-left: 0;
}

.no-margin-right {
  margin-right: 0;
}

.margin-right-5 {
  margin-right: 5px;
}

// Horizontal Rules
.hr-5 {
  margin: 5px;
}

.hr-10 {
  margin: 10px;
}

// Borders
.no-border-top {
  border-top: 0 !important;
}

// Widths
.width-percent-70 {
  width: 70%;
}

.width-percent-100 {
  width: 70%;
}

// Miscellaneous
.strong-password-rules {
  background-color: #ddd;
  padding: 10px 20px;
  border-radius: 5px;
  color: #333;
  margin-bottom: 10px;
  font-size: .9em;

  h3 {
    margin-top: 0;
    color: #111;
  }

  ul {
    margin-bottom: 0;
    margin-left: 10px;
  }
}

.center {
  text-align: center;
}

.release-one {
  display: none;
}

//Add a max-width to activate the ellipsis
.text-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}