.inherit-badge {
  display: block;
  font-weight: normal;
  font-size: 12px;
  color: $gray-light;
  padding-top: 3px;
}

.inherit-form-field-badge {
  position: relative;
  top: -27px;
  left: 212px;
}

.sub .badge {
  background-color: #428bca;
  font-weight: normal;
}

.sub .badge > a {
  color: white;
}

.badge-lg {
  font-size: 18px;
}

.badge-success {
  background-color: $brand-success;
}

.badge-warning {
  background-color: $brand-warning;
}

.badge-danger {
  background-color: $brand-danger;
}

.badge-primary {
  background-color: $brand-primary;
}