.visitor-rejection-reason,
.visitor-approved,
.visitor-rejected {
    display: none;
}

.visitor-rejection-reason.visible,
.visitor-approved.visible,
.visitor-rejected.visible {
    display: block;
}

.view-automatic {
    display: none;
}

.rejected,
.approved {
    padding-left: 22px;
}

.rejected:before,
.approved:before {
    display: inline-block;
    font-family: 'FontAwesome';
    font-size: 1.1em;
    margin-left: -16px;
    padding-right: 5px;
}

.rejected:before {
    color: $brand-danger;
    content: "\f057";
}

.approved:before {
    color: $brand-success;
    content: "\f058";
}
