.section {
    margin-bottom: 20px;
}

.panel-no-record-selected {
    margin-top: 359px;
    text-align: center;
}

.panel-heading {
    border-bottom: 0;
    b {
        font-weight: bold;
        width: calc(100% - 30px);
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        display: inline-block;
    }
}

.panel-filters {
    .js-toggle-filters {
        font-size: .8em;
    }
    .panel-title {
        background-color: white;
        border-bottom: 0;
        padding: 8px;
        border-top-left-radius: $border-radius-base;
        border-top-right-radius: $border-radius-base;
    }
    .panel-body {
        padding: 0;
    }
    .filter-search input[type="text"],
    .filter-search input[type="search"] {
        border-radius: 0;
    }
    .panel-header {
        margin: 0 auto;
        color: $gray;
        background-color: #eee;
        font-weight: 500;
        font-size: 14px;
        border-top: 1px solid #e0e0e0;
        a {
            display: block;
            color: inherit; //Use same color as .panel-header
            padding: 8px;
        }
    }
}

.panel-filter-summary {
    h3 {
        margin-top: 0;
    }
    h4 {
        margin-top: 0;
    }
}

.well {
    font-weight: normal;
    padding: 10px;
    vertical-align: middle;
    background: lighten($sui-gray, 67%);
    color: #333;
    border: none;
    box-shadow: none;
}

.securus-panel-style {
    @include box-shadow(0 1px 6px rgba(0, 0, 0, 0.175));
    background-color: white;
    &.securus-panel-padding {
        padding: 20px;
        + .section-header {
            padding-top: 0;
        }
    }
}

.settings-panel-details {
    list-style: none;
    margin: 5px 0 0 0;
    padding: 0;
    li {
        position: relative;
        display: block;
        border-bottom: 1px solid #eee;
        padding: 4px 0;
        i {
            width: 25px;
            text-align: center;
            margin-right: 5px;
            &.fa-check-circle {
                color: $brand-success;
            }
        }
        span.badge {
            margin-right: 5px;
            float: right;
        }
        &:last-child {
            border-bottom: none;
        }
        &:first-child {
            margin-top: 10px;
        }
    }
}

.card {
    .panel-heading {
        border-bottom: 0;
        a {
            display: block;
        }
        b {
            font-weight: bold;
            width: calc(100% - 30px);
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            display: inline-block;
        }
        span {
            display: inline-block;
        }
    }
    .panel-body {
        max-height: 140px;
    }
    .panel-footer {
        border-top: 1px solid $gray-lighter;
        background-color: transparent;
    }
}

.panel-default > .permission-panel-bg {
    background-color: $sui-link-color;
    color: $white;
    border: none;
}

.detail-panel-show {
    margin-top: -5px;
    span + span {
        display: none;
    }
}

.view-modified {
    margin-top: 15px;
    font-style: italic;
    font-size: 14px;
}

.view {
    .item {
        padding: 2px 5px;
        margin: 0 3px;
        &.active {
            background-color: $securus-primary-blue;
            border-radius: $border-radius-base;
            color: white;
        }
    }
}

.table-panel {
    tbody {
        tr {
            td:first-child {
                width: 50%;
            }
        }
        tr:first-child {
            td {
                border-top: none;
            }
        }
    }
}

.right-panel-style {
    background-color: $white;
    border-right: 1px solid #d4d4d4;
    border-left: 1px solid #d4d4d4;
    border-bottom: 1px solid #d4d4d4;
    @include box-shadow(0 1px 6px rgba(0, 0, 0, 0.175));
}

.right-panel-content {
    margin-top: 0;
    padding: 15px;
}

.right-panel-tab {
    background-color: $gray-lighter;
}

.panel-sidebar {
    background: #eee;
    h1 {
        color: #666;
        padding-bottom: 10px;
        margin-top: 5px;
        margin-bottom: 20px;
        border-bottom: 1px solid #ddd;
    }
    label {
        font-weight: normal;
        color: #888;
    }
    > .actions {
        margin-top: 30px;
    }
}

.panel-no-row-selected,
.panel-no-call-selected {
    display: none;
    margin-top: 359px;
    text-align: center;
    margin: 20px;
    border-color: lighten($sui-gray, 50%);
    color: lighten($sui-gray, 40%);
    p {
        margin: 0;
    }
}

.panel-group .panel-body {
    overflow-y: auto;
}

.panel-action {
    background: $gray-lighter;
    margin: 12px 0;
    padding: 5px 15px;
}

.right-panel-content .panel-action {
    background-color: transparent;
}
