$SourceSansPro: 'Source Sans Pro';
@mixin font-family-SourceSansPro {
  font-family: $SourceSansPro, Arial, Helvetica, sans-serif;
}


@font-face {
  //font-family: 'Source Sans Pro Extra Light';
  font-family: $SourceSansPro;
  font-weight: 100;
  src: url('../fonts/SourceSansPro/SourceSansPro-ExtraLight.otf.woff'); /* Chrome 6+, FireFox 3.6+, IE 9+, Safari 5.1+ */
}

@font-face {
  //  font-family: 'Source Sans Pro Light';
  font-family: $SourceSansPro;
  font-weight: 300;
  src: url('../fonts/SourceSansPro/SourceSansPro-Light.otf.woff'); /* Chrome 6+, FireFox 3.6+, IE 9+, Safari 5.1+ */
}

@font-face {
  //  font-family: 'Source Sans Pro Regular';
  font-family: $SourceSansPro;
  font-weight: 500;
  src: url('../fonts/SourceSansPro/SourceSansPro-Regular.otf.woff'); /* Chrome 6+, FireFox 3.6+, IE 9+, Safari 5.1+ */
}

@font-face {
  //  font-family: 'Source Sans Pro Italic';
  font-family: $SourceSansPro;
  font-weight: 500;
  font-style: italic;
  src: url('../fonts/SourceSansPro/SourceSansPro-It.otf.woff'); /* Chrome 6+, FireFox 3.6+, IE 9+, Safari 5.1+ */
}

@font-face {
  //  font-family: 'Source Sans Pro Bold';
  font-family: $SourceSansPro;
  font-weight: 700;
  src: url('../fonts/SourceSansPro/SourceSansPro-Bold.otf.woff'); /* Chrome 6+, FireFox 3.6+, IE 9+, Safari 5.1+ */
}

@font-face {
  //  font-family: 'Source Sans Pro Bold Italic';
  font-family: $SourceSansPro;
  font-weight: 700;
  font-style: italic;
  src: url('../fonts/SourceSansPro/SourceSansPro-BoldIt.otf.woff'); /* Chrome 6+, FireFox 3.6+, IE 9+, Safari 5.1+ */
}

@font-face {
  font-family: 'Montserrat';
  font-weight: normal;
  src: url('../fonts/Montserrat/Montserrat-Regular.ttf'); /* Chrome 6+, FireFox 3.6+, IE 9+, Safari 5.1+ */
}

@font-face {
  font-family: 'Montserrat';
  font-weight: bold;
  src: url('../fonts/Montserrat/Montserrat-Bold.ttf'); /* Chrome 6+, FireFox 3.6+, IE 9+, Safari 5.1+ */
}


body {
  @include font-family-SourceSansPro;
  font-weight: 500;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  @include font-family-SourceSansPro;
  font-weight: 100;
}
i,
em {
  @include font-family-SourceSansPro;
  font-style: italic;
  font-weight: 500;
}
strong,
b {
  @include font-family-SourceSansPro;
  font-weight: 700;
}
strong em,
em strong,
i b,
b i {
  @include font-family-SourceSansPro;
  font-style: italic;
  font-weight: 700;
}
input {
  @include font-family-SourceSansPro;
}


