
.dev {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  margin: 0;
  padding: 20px;
  z-index: 9999;
  text-align: center;
  border: 0;
  border-radius: 0;
  box-shadow: 0 0 2px 1px rgba(0,0,0,.1);
}

.dev,
.dev-mode-on,
.dev-mode-off {
  display: none;
}
.dev.visible,
.dev-mode-off.visible,
.dev-mode-on.visible {
  display: inline-block;
}

.dev-options {
  display: inline-block;
  margin-right: 20px;
}
.devmode-view-link,
.dev-option {
  padding: 2px 10px;
  text-decoration: none !important;

  &.selected {
    font-size: inherit;
    background-color: #8a6d3b;
    color: white;
    border-radius: 10px;

    &:hover,
    &:active{
      text-decoration: none !important;
    }
  }
}