.security-role {
    list-style: none;
    margin: 10px 0;
    padding: 0;
    li {
        background-color: #d9edf7;
        margin: 2px;
        padding: 8px 16px;
        border-radius: 4px;
        border: 1px solid #bce8f1;
        a {
            color: $brand-primary;
            cursor: pointer;
            &.delete {
                color: $brand-primary;
                &:hover {
                    color: $brand-danger;
                    transition: 0;
                }
            }
        }
    }
}
.panel-no-role-selected {
    margin-bottom: 157px;
    text-align: center;
    margin-top: 160px;
}
.select-role {
    label {
        margin: 0 0 5px 0;
    }
}
.select-role-height {
    height: 291px;
}
.section-divider {
    margin-top: 35px;
}
.security-modal-divider {
    border-left: 1px solid #ddd;
}
.security-role-desc {
    border-bottom: 1px solid #ddd;
    padding-bottom: 10px;
}
.security-role-select-nav {
    li {
        margin-right: 10px;
        &.active > a:after {
            content: '\2192';
            float: right;
        }
        label {
            display: inline-block;
        }
        a {
            display: inline-block;
            width: 210px;
        }
    }
}
