.biometrics {
  h5 {
    position: relative;
    font-size: 1.2em;
    font-weight: 700;
  }
}

.biometric-recordings {
  margin-top: 12px;
  margin-bottom: 2px;

  .title {
    margin: 8px 0 0 0;
    font-size: 1em;
    font-weight: 500;
  }
}

.biometric-enrollment {
  .title {
    .pre {
      display: block;
      text-transform: uppercase;
      font-size: .8em;
      font-weight: 500;
      color: #666;
    }
    .post {
      display: block;
      font-weight: 500;
      font-size: .8em;
      margin-top: 3px;
    }
  }
}

.biometric-enrollment {
  padding-bottom: 24px;
  border-bottom: 1px solid #eee;
  margin-bottom: 24px;

  &:last-child {
    margin-bottom: 0;
    border-bottom: 0;
  }
}

.biometric-btn-group {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  border-collapse: collapse;
  margin-bottom: 12px;

  .biometric-btn-play {
    position: relative;
    margin-right: 3px;
    margin-top: 3px;
    position: relative;
    display: block;
    float: left;
    width: 60px;
    height: 60px;
    text-align: center;
    border: 1px solid #ccc;
    background-color: white;

    &:hover {
      background-color: $brand-primary;
      color: white;
      * {
        color: white;
      }
    }

    .label {
      display: block;
      color: #fff;
      position: absolute;
      left: calc(50% - 2em);
      top: calc(50% - 2em);

      &.stop {
        display: block;
        color: $brand-primary;
        position: absolute;
        left: calc(50% - 1.5em);
        top: calc(50% - 1.2em);
      }

      &.play {
        display: block;
        color: $brand-primary;
      }

    }
    .status {
      position: absolute;
      bottom: 0;
      right: 0;
      border-top-left-radius: 24px;
      width: 24px;
      height: 24px;
      text-align: right;
      line-height: 24px;
      padding-right: 2px;
      padding-top: 2px;
      border: 0;
      color: white;

      &.accepted {
        //        background-color: $brand-primary;
        background-color: $brand-success;
        &:before {
          content: "";
        }
      }
      &.rejected {
        background-color: #BBB;
        &:before {
          content: "";
        }
      }
    }
  }
}