.counter-section {
    margin: 5px 0;
    .value {
        display: block;
        margin-top: auto;
        text-align: center;
        font-size: 18px;
        &.success {
            color: $brand-success;
        }
        &.warning {
            color: $brand-warning;
        }
        &.danger {
            color: $brand-danger;
        }
        &.primary {
            color: $brand-primary;
        }
        &.calls {
            color: rgba(0, 116, 200, .6);
        }
        &.small {
            font-size: 18px;
        }
    }
    .desc {
        display: block;
        text-align: center;
    }
}

.widget-style {
    background-color: white;
    border-radius: 4px;
    border: 1px solid #eee;
    padding-left: 10px;
    padding-right: 10px;
    height: 240px;
}

.timeline-separator {
    margin-top: 5px;
    border-top: 1px solid #eee;
    > a {
        margin-top: 5px;
    }
}

.dashboard-counters .section-header {
    text-align: center;
}

.dashboard-counters .value {
    display: block;
    font-size: 2.4em;
    text-align: center;
    color: #16a085;
}

.dashboard-counters .desc {
    display: block;
    text-align: center;
    color: #2c3e50;
}

.dashboard-counters .summary {
    text-align: center;
    font-size: .9em;
    margin-top: 5px;
    text-transform: uppercase;
    color: #aaa;
}

.counter-section div {
    border-right: 1px solid #eee;
    padding: 5px;
}

.counter-section div:last-child {
    border-right: 0;
}

.dashboard-counter {
    height: 200px;
}

.panel-dashboard {
    padding: 16px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
}

.btn-approve {
    border: 1px solid $brand-success;
    background: transparent;
    color: $brand-success;
    &:hover {
        background: $brand-success;
        color: white;
        transition: none;
    }
}

.btn-reject {
    border: 1px solid $brand-danger;
    background: transparent;
    color: $brand-danger;
    border-right: 0;
    &:hover {
        background: $brand-danger;
        color: white;
        transition: none;
    }
}
