.section-header {
  padding-top: 10px;
  padding-bottom: 10px;
  margin-bottom: 10px;
  position: relative;
  border-bottom: 1px solid #eee;


  h1,
  h2,
  h3,
  h4 {
    margin: 0;
    padding: 0;
  }

  & + hr {
    margin-top: 0;
    margin-bottom: 0;
    border: 0;
  }

  hr {
    clear: both;
  }

  .btn {
    margin-bottom: 6px;
  }

  .btn-link {
    margin-bottom: 2px;
  }

  .section-header-action {
    position: absolute;
    right: 0;
    bottom: 0;
    font-size: $font-size-base;
  }

  .section-header-left {
    display: block;
    float: left;
    width: 20%;
    text-align: left;
    margin-bottom: 10px;
  }
  .section-header-middle {
    display: block;
    float: left;
    width: 60%;
    text-align: center;
    margin-bottom: 10px;
  }
  .section-header-right {
    display: block;
    float: left;
    text-align: right;
    width: 20%;
    margin-bottom: 10px;
  }
}

.section-header + hr {
  border: 0;
}

.header-action {
  position: absolute;
  right: 0;
  bottom: 0;
  font-size: .8em;
  font-weight: 500;
}

.selected-seperator {
  margin: 20px 0 10px 0;
  border-bottom: 1px solid #ddd;
  padding: 0 0 5px 0;
}

h4.selected-seperator {
  margin: 10px 0;
  border-bottom: 1px solid #ddd;
  padding: 0 0 5px 0;
}
