.no-breadcrumb {
  padding-top: 80px;
}

.breadcrumb-container,
.breadcrumb {
  background: $gray-lighter;
  margin: 0;
}

.breadcrumb-container {
  margin-bottom: 20px;
  padding-top: 65px;
}

