.calendar-snapshot {
.fc-toolbar {
        display: none;
    }

.fc-slats td {
        height: .2em;
        border: 0;
    }

.fc-axis {
        display: none;
    }

.fc-slats table {
        border-top: 1px solid #ddd;
        border-bottom: 1px solid #ddd;
    }

.fc td,
.fc th {
        border: 0;
    }

.fc tr:nth-child(odd) td {
        border-bottom: 1px solid #eee;
    }

.fc tr:nth-child(even) td {
        border-bottom: 1px solid #eee;
    }

.fc-time-grid-event.fc-event.fc-start.fc-end {
        background: #1aab60;
        border: 0;
        border-radius: 0;
    }
}