.inmate-details-container {
    h4 {
        margin-left: 5px;
    }
}
.inmate-activity-header {
    margin: 0;
}
.inmate-profile-covert-alerts {
    .covert-alert-number {
        font-size: 40px;
        line-height: 1em;
        font-weight: bold;
        margin-right: 5px;
    }
    .covert-alert-label {
        font-size: 14px;
        margin-top: -19px;
        margin-bottom: 10px;
    }
    hr {
        margin: 10px 0;
    }
}
.inmate-debit-balance {
    font-size: 20px;
}
.inmate-profile-notes {
    a.pull-right {
        margin-top: 5px;
        font-size: 12px;
    }
}
.row {
    position: relative;
}
.expand-icon {
    transition: .5s;
}
.clicked {
    transform: rotate(-180deg);
}
