.page-header {
    padding: 0 20px;
    margin: 0;
    border: none;
    position: fixed;
    background: linear-gradient($page-header, $page-header), url('../images/backdrop1.jpg');
    background-position: 0px 500px;
    width: 100%;
    height: 60px;
    z-index: $page-header-z-index;
    color: white;
    @include box-shadow(0px 3px 2px 0px rgba(0, 0, 0, 0.2));
    .container {
        white-space: nowrap;
    }
    .title,
    .pull-left {
        float: left;
        white-space: nowrap;
        width: 70%;
    }
    .actions,
    .pull-right {
        float: right;
        margin-top: 14px;
        > * {
            display: inline-block;
            vertical-align: middle;
        }
        .btn {
            color: white !important;
        }
    }
    .media {
        overflow: initial;
        zoom: reset;
    }
    .media,
    .icon,
    .desc,
    .page-header-icon,
    h2 {
        display: inline-block;
        vertical-align: middle;
        line-height: 1;
        margin: 6px 0 0 0;
    }
    .icon,
    .page-header-icon {
        background-color: $white;
        border-radius: 50%;
        width: 48px;
        height: 48px;
        padding: 10px;
        margin-right: 10px;
        text-align: center;
        color: $page-header;
    }
    .desc,
    h2 {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 90%;
        text-transform: uppercase;
        font-weight: 700;
    }
    h2.media {
        vertical-align: top;
        margin-top: 10px;
        margin-left: 20px;
    }
    //---- Styles for Headers with Photos e.g. Inmate Profile ----
    &.photo {
        .media, .photo {
            float: left;
            height: 0;
            margin-top: 20px;
            margin-right: 20px;
            color: #121212;
        }
        .title {
            width: 45%;
        }
    }
}
