.cdr-actions {
  width: 75%;
  text-align: right;
  white-space: nowrap;
}
.cdr-actions .add-tag-group {
  width: 60%;
}
.cdr-action {
  display: inline-block;
}
.add-tag-group {
  display: none;
  white-space: nowrap;
}
.add-tag-group,
.result-options {
  display: inline-block;
  vertical-align: middle;
}
.btn-tag {
  vertical-align: top;
  width: auto;
}
.cdr-end-time {
  display: none;
}

.panel.panel-tertiary .panel-body {
  background: $flat-midnight-blue;
  color: $white;
  border-radius: 4px;

  h3 {
    font-weight: 600;
    font-size: 16px;
    line-height: 35px;
  }

  label {
    font-weight: normal;

    input {
      margin-right: 10px;
    }
  }

  h4 {
    font-size: 15px;
  }

}

.filter-search {
  position: relative;
  width: 100%;

  input[type="text"] {
    border: 1px solid #eee;
  }
}

.filter-search-icon {
  margin-left: -200px;
  float: right;
  margin-top: -25px;
  margin-right: 5px;
}

.filter-select {
  padding: 10px;
  max-height: 150px;
  overflow-y: auto;

  label {
    display: block;
    width: 200px;
    padding: 2px;
    font-weight: normal;
    font-size: 13px;

    &:after {
      content: " ";
      display: table;
      clear: both;
    }
  }

  input {
    float: left;
  }

  .wrap {
    float: left;
    display: block;
    width: calc(100% - 20px);
    word-wrap: break-word;
  }

  input[type="checkbox"] {
    margin-right: 6px;
  }
}

.fa-type {
  font-size: 24px;
}

.filter-message {
  padding: 10px;
}

.panel-call-details {
  padding: 0;
  word-wrap: break-word;

  h4 {
    margin-top: 0;
  }

  .panel-body {
    padding: 0;
  }

  .btn-sm {
    margin-top: 10px;
    width:100%;
    background-color: transparent;
    color: $brand-primary;

    &:hover {
      background-color: $brand-primary;
      color: white;
    }
  }

  .details {
    border-bottom: 1px solid #ddd;
    margin-top: 3px;
    font-weight: bold;
  }

  .right-panel-hr {
    margin: 20px 0 10px 0;
    padding-bottom: 3px;
    border-bottom: 1px solid #ddd;
  }
}

.cdr-call-details,
.vdr-video-details {
  [class^="col-md-"] + [class^="col-md-"] {
    padding-left: 0;
  }
}

.filter-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  max-height: 170px;

  ul {
    list-style: none;
  }
}

/* ----------------------- */
/*    Custom Checkboxes    */
/*    Selected Filters     */
/* ----------------------- */

.filters-selected {
  margin-bottom: 10px;
  height: 100px;
  overflow-y: auto;

  h4 {
    margin-top: 0;
    margin-bottom: 4px;
  }
}

.filter-selected {
  background: $brand-primary;
  display: inline-block;
  padding: 1px 8px 1px 0;
  max-width: 95%;
  white-space: nowrap;

  border-radius: 12px;
  margin-right: 2px;
  margin-bottom: 5px;

  input[type=checkbox] {
    display: none;
  }
  .icon {
    font-family: FontAwesome;
    font-style: normal;
    color: rgba(255,255,255, .5);
  }
  .icon:after {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 1.1em;
  }
  input[type=checkbox] + .icon:after {
    content: '\f111';
  }
  input[type=checkbox]:checked + .icon:after {
    content: '\f057';
  }

  .label {
    display: inline-block;
    font-size: .9em;
    max-width: 90%;
    font-weight: 500;
    color: #fff;
    vertical-align: top;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

.filters-selected-group {
  margin-bottom: 10px;
}