a[data-toggle="collapse"],
a[data-toggle="collapse"]:hover {
    text-decoration: none !important;
    display: block;
    color: inherit;
}

[data-toggle="collapse"] .toggle-hide:after,
[data-toggle="collapse"] .toggle-show:after {
    font-family: 'FontAwesome';
    font-size: .7em;
    line-height: 200%;
}

[data-toggle="collapse"] .toggle-hide:after {
    content: '\f077';
}

[data-toggle="collapse"] .toggle-show:after {
    content: '\f078';
}

[data-toggle="collapse"]:not(.collapsed) .toggle-show {
    display: none;
}

[data-toggle="collapse"].collapsed .toggle-hide {
    display: none;
}